.home-run-container{
    width: 100%;
    height: 1300px;
    background-image: linear-gradient( to bottom right, rgb(94, 118, 133), black  );
    background-size: 100%;
    display: inline-block;
    vertical-align: top;
    z-index: 2;
}

.service-offers-container{
    background-image: linear-gradient( to bottom right, rgb(94, 118, 133), black  );
}

.home-run-batter{
    width: 50%;
    height: 750px;
    margin-top: 180px;
}

.technology-information-container{
    width: 50%;
    height: 750px;
    display: inline-block;
    vertical-align: top;
    z-index: 2;
}

.tic-title{
    text-align: center;
    color: white;
    margin-top: 20px;
    height: 100px;
    
}

.technology-information-box-l{
    width: 300px;
    height: 300px;
    background-color: rgba(251, 217, 154, 0.726);

    transform: rotate(45deg);
    margin-top: 50px;
    margin-left: 100px;
    padding: 50px;
}

.technology-information-box-r{
    width: 300px;
    height: 300px;
    background-color: rgba(251, 217, 154, 0.726);
   

    transform: rotate(45deg);
    margin-top: 50px;
    margin-left: 450px;
    padding: 50px;
}

.info-box-context{
    transform: rotate(-45deg);
    color: rgb(231, 227, 227);
    /* border: 2px solid black; */
    text-align:center;
    margin-left: -25px;
    width: 250px;
    margin-top: 24px;
    /* background-color: rgba(0,0,0,0.05); */
    border-radius: 10px;
    padding: 10px;
    font-size: 12px;
}


.info-box-context h3{
    color:white;
    /* background-color: red; */
    font-size: 25px;
    margin-bottom: 20px;
}

