@media only screen and (max-width: 1663px) {
    .home-run-batter{
        margin-top:250px;
        height: 500px;
    }

    .technology-information-box-l{
        width: 250px;
        height: 250px;
        background-color: rgba(251, 217, 154, 0.726);
    
        transform: rotate(45deg);
        margin-top: 50px;
        margin-left: 50px;
        padding: 50px;
    }
    
    .technology-information-box-r{
        width: 250px;
        height: 250px;
        background-color: rgba(251, 217, 154, 0.726);
       
    
        transform: rotate(45deg);
        margin-top: 50px;
        margin-left: 150px;
        padding: 50px;
    }

    .info-box-context{
        transform: rotate(-45deg);
        color: rgb(231, 227, 227);
        /* border: 2px solid black; */
        text-align:center;
        margin-left: -55px;
        width: 250px;
        margin-top: -5px;
        /* background-color: rgba(0,0,0,0.05); */
        border-radius: 10px;
        padding: 10px;
        font-size: 16px;
    }
}

@media only screen and (max-width: 985px) {
    .home-run-batter{
        margin-top:250px;
        height: 500px;
        width:40%;
    }

    .technology-information-container{
        width: 60%;
        height: 750px;
        display: inline-block;
        vertical-align: top;
        z-index: 2;
    }

}

@media only screen and (max-width: 820px) {
    .tic-title{
        margin-bottom: 40px;
    }

    .home-run-batter{
        margin-top:250px;
        height: 500px;
        width:80%;
        margin-left:auto;
        margin-right:auto;
        display:block;
    }

    .technology-information-container{
        width: 80%;
        height: 750px;
        display:block;
        margin-right:auto;
        margin-left: auto;
        display:block;
        z-index: 2;
    }

    .home-run-container{
        height:1900px;
    }


}

@media only screen and (max-width: 600px){

    .technology-information-box-l{
