.math-eq{
    /* background-color: rgba(255,255,255,0.2); */
    color: white;
    opacity: 0.3;
    position: absolute;
}

.eq-one{
    left: 7%;
    top: 1350px;
}

.eq-two{
    left: 80%;
    top: 1200px;
}
.eq-three{
    left: 42%;
    top: 1620px;
}

.eq-four{
    left: 32%;
    top: 2270px;
}

.eq-five{
    left: 90%;
    top: 2150px;
}

@media only screen and (max-width: 1400px) {
    .eq-five{
        left:75%;
    }
}
@media only screen and (max-width: 820px) {
    .eq-two{
        top:1260px;
        left:60%;
    }
    .eq-one{
        top:1440px;
    }
    .eq-three{
        left: 70%;
    }
    .eq-five{
        left:5%;
        top:2370px;
    }
    .eq-four{
        left:60%;
    }
}