.angled-intro{
    width: 120%;
    height: 180px;
    top: -35px;
    left: -5%;
    position: absolute;
    transform: rotate( 2deg );
    background-color: rgb(14, 52, 75);
    z-index: 0;
    background-image: linear-gradient( to bottom right, rgb(53, 72, 83), black  );
}

