.footer-container{
    background-color: rgb(50, 52, 58);
    color: rgb(88, 223, 228);
}

ul { list-style: none; } 
li::before {
    content: "\23F4"; /* Unicode bullet symbol */
    color: rgb(88, 223, 228); /* Bullet color */
	
	/* Optional tweaks */
    font-weight: bold;
}

.links-tab{
    list-style: none; 
    width: 50%;
    padding: 30px;
    padding-top: 10px;
    display: inline-block;
    vertical-align: top;
}

.links-tab ul{
    padding: 15px;
}

.work-samples li{
    list-style-type: circle;
    color: rgb(88, 223, 228);
}

@media only screen and (max-width: 680px) {
    .links-tab{
        list-style: none; 
        width: 100%;
        display: block;
    }
  }

  