
.navbar-top{
    margin-top: 0px;
    background-color: rgb(7, 22, 22);
    height: 30px;
    width: 100%;
    margin: 0px;
    top: -30px;
    text-align: right;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 2;
}

.navbar-top a:hover{
    color: white;
}

.navbar-top-txt{
    color:rgb(163, 216, 212);
    margin-right: 10px;
    margin-top: 2px; 
}

.navbar-top-txt a{
    text-decoration: none;
    color: inherit;
}

.navbar{
    background-color: rgb(12, 42, 42);
    border-top: 5px solid rgb(19, 104, 99);
    border-bottom: 5px solid rgb(19, 104, 99);
    width: 100%;
    height: 80px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 2;
}

.options div > a:after { content: ' »'; }
.options > div > a:after {content: ' »'; }
.options div > a:only-child:after {content: ''; }

.navbar-element{
    position: relative;
    display:inline-block;
    font-size: 20px;
    text-decoration: none;
    color: rgb(163, 216, 212);
    font: Georgia, serif;
    margin-right: 30px;
}

.navbar-element:hover{
    color: white;
}

.navbar-element a{
    text-decoration: none;
    color: inherit;
}

/* for dropdown menu */
.dropdown-content { 
    display: none;
    position: absolute;
    background-color: rgb(12, 42, 42);
    border: 2px solid rgb(19, 104, 99);
    color: rgb(163, 216, 212);
    width: 110px;
    text-align:center;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    padding: 10px;
    z-index: 10;
}

.dropdown-content p{
    margin-top: 2px;
    margin-bottom: 2px;
}

.dropdown-content p a{
    text-decoration: none;
    color: inherit;
}

.dropdown-content p a:hover{
    color:white;
}

.navbar-element:hover .dropdown-content {
    display: block;
}

.title-text{
    display: inline-block;
    margin-left: 55px;
}

@media only screen and (max-width: 680px) {
    .navbar{
        text-align:center;
        height:110px;
    }
    .navbar-title{
        display: block;
        position:relative;
        color: rgb(189, 217, 233);
        margin-bottom: 15px;
        margin-top: 10px;
    }

    .navbar-options{
        display: block;
        position: relative;
        margin-left: auto;
        margin-right:auto;
        width: 300px;
        
    }

    .title-icon{
        height: 40px;
        width: 40px;
        display: inline-block;
        margin: 0px;
        margin-top: 2px;
        margin-left: 10px;
        position: absolute;
        top: 0%;
        /* -ms-transform: translateY(-20%);
        transform: translateY(-20%); */
        color: rgb(189, 217, 233);
    }
}

@media only screen and (min-width: 681px) {
    .navbar-title{
        margin: 0px;
        margin-left: 10px;
        position: absolute;
        top: 40%;
        -ms-transform: translateY(-50%);
        transform: translateY(-50%);
        color: rgb(189, 217, 233);
    }

    .options{
        text-align: right;
        right: 0;
        top: 0;
        display: inline-block;
        width: 450px;
        position: absolute;
        top: 50%;
        -ms-transform: translateY(-50%);
        transform: translateY(-50%);
    }

    .title-icon{
        height: 40px;
        width: 40px;
        display: inline-block;
        margin: 0px;
        margin-top: 5%;
        margin-left: 10px;
        position: absolute;
        top: 40%;
        -ms-transform: translateY(-50%);
        transform: translateY(-50%);
        color: rgb(189, 217, 233);
    }
}
