    .carousel-image{
        width: 100%;
        max-width: 1080px;
        height: 500px;
        display: block;
        margin-left: auto;
        margin-right: auto;
        margin-top: 2%;
        margin-bottom: 2%;
        border: 2px solid rgb(18, 177, 129);
    }

    .wrigley{
        background-image: url('../../assets/wrigley.jpg');
        background-size: 100% 100%;
        background-repeat: no-repeat;
    }

    .meeting{
        background-image: url('../../assets/hectic-meeting.jpg');
        background-size: 100% 100%;
        background-repeat: no-repeat;
    }

    .chicago{
        background-image: url('../../assets/chicago.jpg');
        background-size: cover;
        background-repeat: no-repeat;
    }

    .nhl{
        background-image: url('../../assets/NHL.jpg');
        background-size: 100% 100%;
        background-repeat: no-repeat;
    }

    .carousel-image-container{
        width:100%;
        display: block;
        margin-left: auto;
        margin-right: auto;
        background-color: rgb(103, 107, 117);
        border-top: 5px solid rgba(28, 155, 167, 0.493);
        border-bottom: 5px solid rgba(28, 155, 167, 0.493);
    }

    .widget-container{
        width:200px;
        height: 100px;
        position:absolute;
        top: 2850px;
        margin-left: auto;
        margin-right: auto;
        left: 0;
        right: 0;
        text-align: center;
    }

    .left-selector{
        width: 30px;
        height: 30px;
        position: absolute;
        top: 2700px;
        background: url("../../assets/pointer.png");
        background-size: 100%;
        transform:scaleX(-1);
    }

    .right-selector{
        width: 30px;
        height: 30px;
        position: absolute;
        top: 2700px;
        background: url("../../assets/pointer.png");
        background-size: 100%;
    }

    .widget-button{
        display: inline-block;
        border-radius: 10px;
        border: 2px solid rgb(21, 23, 46);
        width:10px;
        height:10px;
        margin-left: 5px;
        background-color: rgb(20, 22, 39);
    }

    .highlighted-button{
        background-color:white;
        border: 2px solid white;
    }

    @media only screen and (max-width: 1080px) {
        .carousel-image{
            margin-top: 0%;
            margin-bottom: 0%;
        }
        .wrigley{
            background-size:cover;
        }
        .meeting{
            background-size:cover;

        }
        .chicago{
            background-size:cover;

        }
        .nhl{
            background-size:cover;
        }
      }

      @media only screen and (max-width: 820px) {
        .widget-container{
            top: 3450px;
        }
        .left-selector{
            top: 3300px;
        }
        .right-selector{
            top: 3300px;
        }
    }




